@use './fonts';
@use './_variables';
@use './hero';
@use './menu';
@use './specializations';
@use './advantages';
@use './contact';

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html, body{
  min-height: 100vh;
  height: 100%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

main{
  padding: variables.$space-4;
}

@media (min-width: variables.$tablet){

  main{
    padding: variables.$space-16;
  }


}