@use './_variables';

.advantages{

  &__heading{
    margin-bottom: variables.$space-2;
  }

  &__subheading{
    color: #000;
    font-weight: 400;
  }

  &__image{
    margin: variables.$space-4 auto variables.$space-8 auto;
  }
}

@media (min-width: variables.$tablet){
  .advantages{
    display: flex;
    justify-content: space-between;
    &__split-wrapper{
      flex: 1;
    }
  }
}