@use './_variables';
.hero{
  height: 100vh;
  width: 100vw;
  background: url("../images/dark/loose-cables-L.jpg")  center center ;
  background-size: cover;
  position: relative;
  padding: variables.$space-4;

  &__title{
  color: #fff;
    position: absolute;
    left: variables.$space-4;
    bottom: variables.$space-12;
    &--accent{
      color: variables.$color-primary-200 ;
    }
}
}

@media (min-width: variables.$tablet){
  .hero{
    position: relative;
    padding: variables.$space-8 variables.$space-16 variables.$space-16 variables.$space-16;
    &__title{
      font-size: 6rem;
      position: absolute;
      left: variables.$space-16;
      bottom: variables.$space-16;
    }
  }
}