@use './_variables';

.hamburger{
  position: absolute;
  top: 0;
  left: 0;
  z-index: 101;
  margin: variables.$space-4;

}

.menu{
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 100;
  background-color: variables.$color-primary-800;
  transform: translateX(-101%);
  transition: transform 600ms ease-in-out;
  &__list{
    list-style-type: none;
    margin-top: variables.$space-12;
  }

  &__item{
    margin: variables.$space-4 ;
    &:first-child{
      margin:0;
    }
  }
  &__link{
  color: #fff;
    font-size: 1.5rem;
    text-decoration: none;
  }

  &__logo{
    color:#fff;
    font-weight: 800;
    font-size: 3rem;
    margin-left: variables.$space-4;

    &--light{
      font-weight: 300;
    }
  }

  &--open{
    transform: translateX(0);
  }
}

@media (min-width: variables.$tablet){
  .hamburger{
    display: none;
  }
  .menu{
    position: static;
    width: 100%;
    //left: 0;
    background-color: transparent;
    transition: none;
    transform: translateX(0);

    &__list{
      display: flex;
      align-items: baseline;
      margin-top:0;
    }

    &__item{
        font-weight: 600;
      &:first-child{
        margin-right: auto;
      }


    }

    &__link{
      padding: variables.$space-2;
      &:hover{
        text-decoration: underline;
      }
    }

    &__logo{
      &:hover{
        text-decoration: none;
      }
      margin-left: 0;
    }

    &--open{
      transform: translateX(0);
    }
  }
}
