@use './_variables';

.contact{
  &__card{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: variables.$space-2;
    border: 1px solid rgba(0,0,0,0.5);
    margin: variables.$space-3 0;
    font-size: 0.8rem;
    &:hover{
      @include variables.shadow-normal;
    }
  }

  &__logo{
    color: variables.$color-primary-800;
    font-weight: bold;
    margin-bottom: variables.$space-1;
    &--font-weigth-light{
      font-weight: lighter;
    }
  }
  
  &__owner{
    font-weight: lighter;
    font-size: 22px;
  }

  a{
    color: inherit;
  }

  &__heading{
    margin: variables.$space-1 0;
  }

  svg{
    margin-right: variables.$space-2;
  }
}

@media (min-width: variables.$tablet){
  .contact{
    &__grid{
      display: flex;

    }
    &__card{
      font-size: 1.2rem;
      margin-right: variables.$space-9;
    }
  }
}