@use './_variables';


.specializations{
  &__category{
    padding: variables.$space-12 variables.$space-4 variables.$space-4 variables.$space-4;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 360px;
    margin: variables.$space-5 0;
    color: #fff;
    background-size: cover;
    background-position: center center;
    @include variables.shadow-normal;

    &:hover{
      @include variables.shadow-hover;
    }



    &-title{
      margin-bottom: variables.$space-1;
      color: variables.$color-primary-200;
    }


    &--electrical-installations{
      background-image: url("../images/dark/electrical-installations-S.jpg");
    }

    &--lighting{
      background-image: url("../images/dark/lighting-S.jpg");
    }

    &--automation{
      background-image: url("../images/dark/automation-S.jpg");
    }

    &--service{
      background-image: url("../images/dark/service-S.jpg");
    }

    &--service{
      background-image: url("../images/dark/service-S.jpg");
    }

    &--repairs{
      background-image: url("../images/dark/repairs-S.jpg");
    }

    &--monitoring{
      background-image: url("../images/dark/monitoring-S.jpg");
    }
  }


  &__grid{
    display: grid;
    gap: variables.$space-10;
    grid-template-columns: 1fr
  }
}

@media (min-width: variables.$tablet){
  .specializations{
    &__grid{
      grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    }

    &__category-text{
      font-size: 1.2rem;
    }
  }
}