$color-primary-200: #8BA5CE;
$color-primary-500: #4970AD;
$color-primary-800: #314D76;


// SPACING
$space-base :8px;
$space-1: $space-base * 1;
$space-2: $space-base * 2;
$space-3: $space-base * 3;
$space-4: $space-base * 4;
$space-5: $space-base * 5;
$space-6: $space-base * 6;
$space-7: $space-base * 7;
$space-8: $space-base * 8;
$space-9: $space-base * 9;
$space-10: $space-base * 10;
$space-11: $space-base * 11;
$space-12:  $space-base * 12;
$space-16:  $space-base * 16;

// SHADOWS
@mixin shadow-normal {
  box-shadow: 0 1px 3px $color-primary-200, 
  0 1px 2px $color-primary-200;
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
}

@mixin shadow-hover{
  box-shadow: 0 1px 3px $color-primary-500, 
  0 1px 2px $color-primary-500;
} 


// BREAKPOINTS
$mobile : 768px;
$tablet: 1024px;
$desktop: 1279px;